<template>
  <div class="mian">
    <canvas id="main" width="1366" height="293"></canvas>
    <canvas id="middle" width="1366" height="293"></canvas>
    <canvas id="final" width="1366" height="293"></canvas>
    <div class="login">
      <el-form
        ref="loginForm"
        :model="loginForm"
        :rules="loginRules"
        class="login-form"
        autocomplete="on"
        label-position="left"
      >
        <div class="title-container">
          <h3 class="title">Rain Star</h3>
        </div>

        <el-form-item prop="username">
          <span class="svg-container">
            <i class="el-icon-user-solid" />
          </span>
          <el-input
            ref="username"
            v-model="loginForm.username"
            placeholder="Username"
            name="username"
            type="text"
            tabindex="1"
            autocomplete="on"
            maxlength="8"
            minlength="2"
          />
        </el-form-item>

        <el-tooltip
          v-model="capsTooltip"
          content="大写已开启"
          placement="right"
          manual
        >
          <el-form-item prop="password">
            <span class="svg-container">
              <i class="el-icon-lock" />
            </span>
            <el-input
              :key="passwordType"
              ref="password"
              v-model="loginForm.password"
              :type="passwordType"
              placeholder="Password"
              name="password"
              tabindex="2"
              autocomplete="on"
              @keyup.native="checkCapslock"
              @blur="capsTooltip = false"
              show-password
              @keyup.enter.native="handleLogin"
              minlength="6"
              maxlength="16"
            />
          </el-form-item>
        </el-tooltip>
        <el-checkbox
          v-model="rememble"
          style="margin: 0px 10px 10px 14px; color: #fff"
          >记住我</el-checkbox
        >
        <el-button
          :loading="loading"
          type="primary"
          style="width: 100%; margin-bottom: 50px; font-size: 16px"
          @click.native.prevent="handleLogin"
          >登<span style="margin: 0 0.5em"></span>入</el-button
        >
        <div v-show="showlog">倒计时{{ showtime }}s</div>
        <div style="position: relative">
          <el-button
            class="thirdparty-button"
            type="primary"
            @click="showDialog = true"
          >
            Or connect with
          </el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { a } from "./js/login";
import { validUsername, validPassword } from "../../utils/validate";
import { StrToMd5 } from "../../utils/md5";
export default {
  mounted() {
    a();
    if (this.loginForm.username === "") {
      this.$refs.username.focus();
    } else if (this.loginForm.password === "") {
      this.$refs.password.focus();
    }
  },
  created() {
    if (localStorage.getItem("username") || localStorage.getItem("passworld")) {
      this.rememble = true;
    }
  },
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!validUsername(value)) {
        callback(new Error("用户名只能用（字母，数字，下划线，减号）"));
      } else {
        callback();
      }
    };
    const validatePassword = (rule, value, callback) => {
      if (!validPassword(value)) {
        callback(
          new Error("密码长度6~16位,且至少含1个大写字母，1个小写字母，1个数字")
        );
      } else {
        callback();
      }
    };
    return {
      showtime: 5,
      rememble: false,
      showDialog: false,
      loginForm: {
        username: localStorage.getItem("username") || "",
        password: localStorage.getItem("password") || ""
      },
      loginRules: {
        username: [
          { required: true, trigger: "blur", validator: validateUsername }
        ],
        password: [
          { required: true, trigger: "blur", validator: validatePassword }
        ]
      },
      passwordType: "password",
      capsTooltip: false,
      loading: false,
      showlog: false,
      redirect: undefined,
      otherQuery: {}
    };
  },
  methods: {
    // settime(i) {
    //   // const clear = [];
    //   // let that = this;
    //   // function time(i) {
    //   //   if (i > 0) {
    //   //     let a = setTimeout(() => {
    //   //       if (i < n) that.showtime = i;
    //   //     }, (n - i) * 1000);
    //   //     clear.push(a);
    //   //     time(i - 1);
    //   //   } else return true;
    //   // }
    //   // if (time(i)) {
    //   //   return true;
    //   // }
    // },
    handleLogin() {
      this.$refs.loginForm.validate(async valid => {
        if (this.rememble) {
          localStorage.setItem("username", this.loginForm.username);
          localStorage.setItem("password", this.loginForm.password);
        }
        if (valid) {
          this.loginForm.password = StrToMd5(this.loginForm.password);
          // let { data } = await this.axios.post("/login/in", this.loginForm);
          // if (data.status == 200) {
          // sessionStorage.setItem("token", data.token);
          this.showlog = true;
          for (let i = 5; i > 0; i--) {
            let T = setTimeout(() => {
              clearTimeout(T); //结束后清理当前定时器
              if (i == 5) {
                this.$router.push("/layout");
              } else this.showtime = 5 - i;
            }, i * 1000);
          }
          this.settoken;
          // await this.axios.get("/api/list", {
          //   params: {
          //     id: this.loginForm.username
          //   }
          // });
        }
        // } else {
        //   console.log("error submit!!");
        //   return false;
        // }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    checkCapslock(e) {
      const { key } = e;
      this.capsTooltip = key && key.length === 1 && key >= "A" && key <= "Z";
    }
  }
};
</script>

<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;
.mian {
  background-color: $bg;
  transition: background-color 1000ms linear;
  height: 100%;
  width: 100%;
  position: absolute;
  .login {
    z-index: 999;
    display: flex;
    position: relative;
    margin: auto;
    padding: 0 35px;
    align-items: center;
    justify-content: center;
    min-height: 100%;
    overflow: hidden;
    .login-form {
      position: relative;
      width: 520px;
      max-width: 100%;
      margin: 0 auto;
      overflow: hidden;
    }

    .tips {
      font-size: 14px;
      color: #fff;
      margin-bottom: 10px;

      span {
        &:first-of-type {
          margin-right: 16px;
        }
      }
    }

    .svg-container {
      padding: 10px 0 10px 14px;
      color: $dark_gray;
      vertical-align: middle;
      display: inline-block;
    }

    .title-container {
      position: relative;

      .title {
        font-size: 26px;
        color: $light_gray;
        margin: 0px auto 40px auto;
        text-align: center;
        font-weight: bold;
      }
    }

    .thirdparty-button {
      position: absolute;
      right: 0;
      bottom: 0;
    }

    @media only screen and (max-width: 470px) {
      .thirdparty-button {
        display: none;
      }
    }
  }
}
canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>

<style lang="scss">
$bg: #283443;
$light_gray: #fff;
$cursor: #fff;

.login {
  .el-icon-user-solid .el-icon-lock {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
  .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;

    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
      caret-color: $cursor;

      &:-webkit-autofill {
        box-shadow: 0 0 0px 1000px $bg inset !important;
        -webkit-text-fill-color: $cursor !important;
      }
    }
  }

  .el-form-item {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
  }
}
</style>
